@import './mixins.scss';

.footer {
    display: flex;
    position: relative;
    justify-content: center;

    height: 5rem;
    padding: 1.3rem;
    border-top: .1rem solid #eaeaea;
}

.footer h1 {
    font-size: 1.4em;
    text-transform: capitalize;
    color: #eaeaea;
    font-weight: 300;
}